import React, { useEffect, useState } from "react";
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Blog from '../components/Blog';
import { Helmet } from 'react-helmet';
import { globalHistory } from "@reach/router";
import Metadata from '../components/Metadata';

const useStyles = makeStyles((theme) => ({
  msgCard: {
    margin: 20,
    padding: 20
  }
}));

export default function BlogPage() {
  const classes = useStyles();
  ///const blogId = "20210312064310"  
  var blogId = undefined;
  if (globalHistory.location.hash !== undefined) {
    blogId = globalHistory.location.hash.replace("#", '');
    console.log(blogId);
  }
  else {
    blogId = false;
    console.log('Part 2');
  }

  return (
    <Layout>
      { (blogId === undefined || blogId === "") ?
        (<Paper elevation={3} className={classes.msgCard} >
          <Typography variant="h5">
            Blog Not Found !
          </Typography>
        </Paper>)
        : (<Blog blogId={blogId} />)
      }
      <Metadata
        Title="Blogs | Bipul Raman"
        Description="Read this Bipul's blog on the associated web-link."
       />
    </Layout>
  );
}